*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5% !important; }

body {
  box-sizing: border-box;
  font-family: VolvoNovum, sans-serif; }

.dt-main {
  height: 90vh;
  padding: 3rem;
  font-size: 1.6rem;
  font-family: VolvoNovum, sans-serif; }
  .dt-main__container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
    .dt-main__container__title {
      font-family: VolvoBradPro, sans-serif;
      display: block;
      font-size: 3rem; }
      .dt-main__container__title--locale {
        font-family: VolvoNovum, sans-serif;
        text-transform: uppercase;
        font-size: 2.8rem;
        font-weight: 300; }

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px; }

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0; }

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s; }

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s; }

input:checked + .slider {
  background-color: #2196F3; }

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3; }

input:checked + .slider:before {
  transform: translateX(26px); }

/* Rounded sliders */
.slider.round {
  border-radius: 34px; }

.slider.round:before {
  border-radius: 50%; }
